import React from 'react';
import TodoItem from './TodoItem';
import PropTypes from 'prop-types';

class Todos extends React.Component{
  render(){
    let todoList = this.props.todos.map(todo => (
      <TodoItem markComplete={this.props.markComplete} delTodo={this.props.delTodo} key={todo.id} todo={todo}/>));
    return (
      <div>
        {todoList}
      </div>
    )
  }
}

//name of the component and the propTypes 
//propTypes is just an object 
Todos.propTypes = {
  //we have a todos prop
  todos: PropTypes.array.isRequired,
}
export default Todos;
