import React, { Component } from 'react';
import PropTypes from 'prop-types';

class TodoItem extends Component {
    //style for the todo list 
    getStyle = () => {
        return {
            textDecoration: this.props.todo.completed ? 'line-through' : 'none',
            background: '#f4f4f4',
            padding: '10px',
            border: '1px solid #ccc',
            marginBottom: '10px',
            display: 'flex',
            justifyContent: 'space-between',
            alignItem: 'center'
        }
    }
    //render goes here 
    render() {
        //console.log(this.props)
        const { id, title } = this.props.todo;
        return (
            <div style={this.getStyle()}>
                <div>
                    <input type="checkbox" style={{marginRight: "5px"}} className="checkbox" onChange={()=>(this.props.markComplete(id))} />
                    <span>{title}</span>
                </div>
                <div>
                    <button className="btn btn-danger btn-sm" style={{float: 'right'}} onClick={this.props.delTodo.bind(this, id)}>Delete</button>
                </div>
            </div>
        );
    }
}

TodoItem.propTypes = {
    todo: PropTypes.object.isRequired,
}

export default TodoItem;