import React, { Component } from 'react'

export class Addtodo extends Component {
    state = {
        title: '',
    }
    handleChange = (e) => {
        this.setState({
            [e.target.name] : e.target.value
        })
    }
    handleSubmit= (e) => {
        e.preventDefault();
        this.props.addTodo(this.state);
        this.setState({
            title: ''
        })
    }
    formStyle = {
        width: '50%',
        margin: '0 auto',
        marginBottom : '10px'
    }
    render() {
        return (
            <div>
                <form className="input-group" style={this.formStyle}>
                    <input onChange={this.handleChange} name="title" value={this.state.title} type="text" className="form-control" placeholder="Add your todo here"/>
                    <div className="input-group-append">
                        <button className="btn btn-success" onClick={this.handleSubmit}>Add</button>
                    </div>
                </form>
            </div>
        )
    }
}

export default Addtodo
